@font-face {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  src: url("/fonts/Inter-Regular.woff2") format("woff2"), url("/fonts/Inter-Regular.woff") format("woff"); }

@font-face {
  font-family: 'Inter';
  font-style: italic;
  font-weight: 400;
  src: url("/fonts/Inter-Italic.woff2") format("woff2"), url("/fonts/Inter-Italic.woff") format("woff"); }

@font-face {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  src: url("/fonts/Inter-Medium.woff2") format("woff2"), url("/fonts/Inter-Medium.woff") format("woff"); }

@font-face {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 700;
  src: url("/fonts/Inter-Bold.woff2") format("woff2"), url("/fonts/Inter-Bold.woff") format("woff"); }

@font-face {
  font-family: 'Inter';
  font-style: italic;
  font-weight: 700;
  src: url("/fonts/Inter-BoldItalic.woff2") format("woff2"), url("/fonts/Inter-BoldItalic.woff") format("woff"); }

.modalWindow {
  height: 100%;
  display: flex;
  flex-direction: column; }

@media (min-width: 830px) {
  .modalWindow {
    height: auto; } }

.modalContent {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto; }

@media (min-width: 830px) {
  .modalContent {
    height: 512px; } }

.tabs {
  width: 100%;
  border-bottom: 1px solid #eef2f5;
  text-align: center; }

.tabs button {
  color: #939ba1;
  position: relative;
  background: transparent;
  border: none;
  text-transform: uppercase;
  font-family: "Inter";
  font-size: 13px;
  height: 2.5rem;
  letter-spacing: 0.0125rem;
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  outline: none;
  transition-property: color;
  transition-duration: 0.4s; }

.tabs button:first-child {
  margin-right: 32px; }

.tabs button:after {
  display: block;
  clear: both;
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  width: 24px;
  border-bottom: 2px solid transparent;
  margin: 0 auto 0 auto;
  transition-property: border-color;
  transition-duration: 0.4s; }

.tabs button.active {
  color: #4360df; }

.tabs button.active:after {
  border-bottom-color: #4360df; }

.dapp {
  height: 48px;
  display: flex;
  align-items: center;
  font-family: "Inter";
  font-weight: 500;
  padding: 0 0.75rem; }

.dapp .image {
  max-width: 24px;
  max-height: 24px;
  border-radius: 50%;
  margin-right: 0.75rem; }

.items {
  display: flex;
  flex-direction: column;
  font-family: "Inter"; }

.items .itemRow {
  height: 32px;
  display: flex;
  align-items: center;
  padding: 0 0.75rem; }

.items .item {
  display: flex;
  align-items: center; }

.items .item img {
  margin-right: 0.75rem; }

.badge, .greenBadge, .redBadge {
  border-radius: 24px;
  color: #ffffff;
  font-family: "Inter";
  font-size: 0.9375rem;
  margin-right: 1rem;
  margin-left: auto;
  padding: 0.1875rem 0.625rem; }

.greenBadge {
  background: #44d058; }

.redBadge {
  background: #f00; }

.inputArea {
  width: calc(100% - 2 * 16px);
  height: 64px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin: auto; }

.inputArea.inputAreaBorder {
  border-bottom: 1px solid #eef2f5; }

.inputArea input {
  width: 19px;
  border: none;
  text-align: center;
  font-size: 2rem;
  line-height: 1.75rem;
  font-family: "Inter";
  margin-right: 0.375rem; }

.inputArea input:focus {
  outline: none; }

.inputArea::after {
  transition: all 0.05s ease-in-out;
  content: 'SNT';
  color: #939ba1;
  font-size: 2rem;
  line-height: 1.75rem;
  font-family: "Inter"; }

.inputArea span {
  font-size: 2rem;
  line-height: 1.75rem;
  font-family: "Inter";
  margin-right: 0.375rem; }

.footer {
  text-align: center; }

.footer button {
  background: #4360df;
  border-radius: 8px;
  color: #fff;
  margin: 0.625rem auto;
  border: none;
  font-family: "Inter";
  padding: 0.6875rem 2.375rem;
  font-size: 0.9375rem;
  cursor: pointer; }

.footer button:disabled,
.footer button[disabled] {
  background: #939ba1; }

.footer .disclaimer {
  font-size: 0.9375rem;
  color: #939ba1;
  line-height: 22px;
  font-family: "Inter";
  padding: 1rem;
  border-bottom: 1px solid #eef2f5;
  margin: 0; }

.footer .disclaimer a {
  text-decoration: none;
  color: #4360df;
  cursor: pointer; }

.learnMoreCnt {
  display: flex;
  flex-direction: column;
  font-family: "Inter"; }
  .learnMoreCnt .title {
    line-height: 40px;
    text-align: center;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 17px;
    border-bottom: 1px solid #f7f7f7; }
  .learnMoreCnt .spacing {
    margin: 14px 16px; }
    .learnMoreCnt .spacing img {
      width: 100%;
      flex: 0 0 auto; }
    .learnMoreCnt .spacing p {
      line-height: 24px;
      text-indent: 32px;
      margin: 0; }
  .learnMoreCnt .backButtonCnt {
    height: 64px;
    display: flex;
    flex: 0 0 auto;
    align-items: center;
    justify-content: center;
    border-top: 1px solid #f7f7f7;
    margin-top: auto; }
    .learnMoreCnt .backButtonCnt .backButton {
      background: #4360df;
      border-radius: 8px;
      color: #fff;
      border: none;
      font-family: "Inter";
      padding: 0.6875rem 2.375rem;
      font-size: 0.9375rem;
      cursor: pointer; }

@media (min-width: 830px) {
  .learnMoreCnt {
    min-height: 512px; } }
