@font-face {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  src: url("/fonts/Inter-Regular.woff2") format("woff2"), url("/fonts/Inter-Regular.woff") format("woff"); }

@font-face {
  font-family: 'Inter';
  font-style: italic;
  font-weight: 400;
  src: url("/fonts/Inter-Italic.woff2") format("woff2"), url("/fonts/Inter-Italic.woff") format("woff"); }

@font-face {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  src: url("/fonts/Inter-Medium.woff2") format("woff2"), url("/fonts/Inter-Medium.woff") format("woff"); }

@font-face {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 700;
  src: url("/fonts/Inter-Bold.woff2") format("woff2"), url("/fonts/Inter-Bold.woff") format("woff"); }

@font-face {
  font-family: 'Inter';
  font-style: italic;
  font-weight: 700;
  src: url("/fonts/Inter-BoldItalic.woff2") format("woff2"), url("/fonts/Inter-BoldItalic.woff") format("woff"); }

.cnt {
  width: 100%;
  display: flex;
  box-sizing: border-box;
  position: fixed;
  left: 0;
  top: 0;
  font-family: "Inter";
  padding: 12px;
  background: #fff;
  box-shadow: 0px 4px 12px rgba(0, 34, 51, 0.08), 0px 2px 4px rgba(0, 34, 51, 0.16);
  transform: translateY(-200%);
  transition-property: transform;
  transition-duration: 0.4s; }
  .cnt .image {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-right: 16px; }
  .cnt .data {
    width: 100%;
    display: flex;
    flex-direction: column; }
    .cnt .data .name {
      display: flex;
      align-items: center;
      position: relative;
      line-height: 22px;
      font-size: 15px;
      font-weight: 700;
      margin-bottom: 2px; }
      .cnt .data .name .nameItself {
        width: 0px;
        flex: 1 1 auto;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        margin-right: 8px; }
      .cnt .data .name .close {
        width: 20px;
        height: 20px;
        line-height: 20px;
        vertical-align: middle;
        flex: 0 0 auto;
        display: flex;
        justify-content: center;
        color: #fff;
        font-size: 16px;
        font-weight: 300;
        font-family: 'Times New Roman', Times, serif;
        margin-left: auto;
        border-radius: 50%;
        background: #939ba1;
        transform: rotate(45deg);
        cursor: pointer; }
    .cnt .data .info {
      line-height: 18px;
      color: #939ba1;
      font-size: 13px;
      margin-bottom: 8px; }
    .cnt .data .status {
      display: flex;
      align-items: center;
      line-height: 15px;
      color: #4360df;
      font-size: 12px; }
      .cnt .data .status img {
        margin-right: 4px; }
    .cnt .data .status.red {
      color: #fa6565; }
    .cnt .data .status.column {
      flex-direction: column;
      align-items: flex-start; }
      .cnt .data .status.column span {
        word-break: break-all; }

.cnt.active {
  transform: translateY(0); }

@media (min-width: 830px) {
  .cnt {
    width: 375px;
    right: 0;
    top: 16px;
    border-radius: 16px;
    margin: auto; } }
