@font-face {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  src: url("/fonts/Inter-Regular.woff2") format("woff2"), url("/fonts/Inter-Regular.woff") format("woff"); }

@font-face {
  font-family: 'Inter';
  font-style: italic;
  font-weight: 400;
  src: url("/fonts/Inter-Italic.woff2") format("woff2"), url("/fonts/Inter-Italic.woff") format("woff"); }

@font-face {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  src: url("/fonts/Inter-Medium.woff2") format("woff2"), url("/fonts/Inter-Medium.woff") format("woff"); }

@font-face {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 700;
  src: url("/fonts/Inter-Bold.woff2") format("woff2"), url("/fonts/Inter-Bold.woff") format("woff"); }

@font-face {
  font-family: 'Inter';
  font-style: italic;
  font-weight: 700;
  src: url("/fonts/Inter-BoldItalic.woff2") format("woff2"), url("/fonts/Inter-BoldItalic.woff") format("woff"); }

.modalWindow {
  height: 100%; }

@media (min-width: 830px) {
  .modalWindow {
    height: auto; } }

a {
  text-decoration: none; }

.container {
  font-family: "Inter";
  background-color: #ffffff; }

.banner {
  height: 8.75rem;
  background-color: rgba(81, 208, 240, 0.15);
  display: flex;
  justify-content: center; }

.image {
  border-radius: 6.25rem;
  width: 6.25rem;
  height: 6.25rem;
  margin: 5rem auto;
  z-index: 99;
  box-shadow: 0px 4px 12px rgba(0, 34, 51, 0.08), 0px 2px 4px rgba(0, 34, 51, 0.16); }

.header {
  color: #000;
  font-size: 0.9375rem;
  line-height: 1.375rem;
  margin-bottom: 0.25rem;
  margin-top: 0.75rem;
  font-weight: 600;
  cursor: pointer; }

.category {
  margin-bottom: 1.25rem; }

.heading {
  color: #939ba1;
  font-size: 0.8125rem;
  line-height: 1.125rem;
  margin-bottom: 0.125rem;
  margin-top: 0;
  max-height: 2.5rem;
  overflow-y: hidden;
  cursor: pointer; }

.information {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 2.5rem auto; }

.button {
  border-radius: 20px;
  background-color: #4360df;
  color: #ffffff;
  padding: 0.75rem 1.25rem; }

.description {
  border-bottom: 1px solid #eef2f5;
  box-shadow: inset 0px 1px 0px #eef2f5;
  padding: 0.625rem 1.25rem; }

.chat {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #eef2f5;
  box-shadow: inset 0px 1px 0px #eef2f5;
  padding: 0.625rem 1.25rem; }

.chat_image {
  width: 2.5rem;
  height: 2.5rem;
  margin-top: 1.25rem;
  border-radius: 20px; }

.chat_link {
  margin: 0.625rem;
  margin-top: 1.5625rem; }

.chat_icon {
  margin-left: -0.625rem;
  margin-bottom: 0.625rem; }

.url {
  border-bottom: 1px solid #eef2f5;
  box-shadow: inset 0px 1px 0px #eef2f5;
  padding: 0.625rem 1.25rem; }

.ranking {
  border-bottom: 1px solid #eef2f5;
  box-shadow: inset 0px 1px 0px #eef2f5;
  padding: 0.625rem 1.25rem; }

.rank {
  display: flex;
  margin-top: 0.625rem; }

.rank_position_1 {
  border-radius: 40px;
  width: 2.5rem;
  margin-right: 0.9375rem;
  height: 2.5rem;
  background-color: #51d0f0;
  display: flex;
  justify-content: center; }

.rank_position_2 {
  border-radius: 40px;
  width: 2.5rem;
  margin-right: 0.9375rem;
  height: 2.5rem;
  background-color: #fa6565;
  display: flex;
  justify-content: center; }

.rank_position_span {
  padding: 0.625rem;
  color: #ffffff;
  font-weight: 600; }

.rank_position_text {
  margin-top: 0.625rem; }

.actions {
  display: flex;
  justify-content: center;
  border-bottom: 1px solid #eef2f5;
  box-shadow: inset 0px 1px 0px #eef2f5;
  padding: 0.625rem 1.25rem; }
  .actions .button {
    cursor: pointer; }
  .actions .button:first-child {
    margin-right: 1.25rem; }

@media (max-width: 356px) {
  .actions .button {
    font-size: 13px; } }
